/* src/components/ProjectModal.css */

/* Center the modal in the middle of the viewport */
.ProjectModal-container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
  }
.modal {
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }
  
  /* Style the modal dialog */
  .modal-dialog {
    max-width: 800px; /* Set maximum width for the modal */
  }
  
  /* Style the modal content */
  .modal-content {
    border-radius: 10px; /* Add rounded corners to the modal content */
  }
  
  /* Style the modal header */
  .modal-header {
    background-color: #007bff; /* Blue header background color */
    color: #fff; /* Header text color */
    border-top-left-radius: 10px; /* Round top-left corner */
    border-top-right-radius: 10px; /* Round top-right corner */
  }
  
  /* Style the modal title */
  .modal-title {
    margin: 0; /* Remove margin around the title */
  }
  
  /* Style the modal body */
  .modal-body {
    padding: 20px; /* Add padding inside the modal body */
  }
  
  /* Style the modal close button */
  .close {
    color: #fff; /* Close button color */
  }
  
  /* Style the modal close button on hover */
  .close:hover {
    color: #fff; /* Close button color on hover */
    opacity: 0.75; /* Reduce opacity on hover */
  }
  
  /* Style the modal image */
  .modal-body img {
    max-width: 100%; /* Make sure the image fits inside the modal */
    height: auto; /* Maintain aspect ratio */
    display: block; /* Center the image horizontally */
    margin: 0 auto 20px; /* Add margin below the image */
  }
  
  /* Style the modal description */
  .modal-body p {
    color: #333; /* Description text color */
    font-size: 16px; /* Description text size */
    line-height: 1.5; /* Description text line height */
  }
  
/* src/components/ProjectsSection.css */

#projects {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin: 50px 0;
  animation: fadeIn 2s ease-out;
}

.heai {
  font-family: 'Times New Roman', Times, serif;
  text-align: center;
  padding-top: 70px;
  color: #ffff;
  animation: slideIn 2s ease-out;
  font-size: 3em;
  text-decoration: underline;
}

.project-list {
  display: flex;
  flex-direction: column;
  grid-gap: 40px;
  gap: 40px; /* Space between projects */
  max-width: 1000px;
  margin: 0 auto;
}

.project-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  grid-gap: 60px;
  gap: 60px; /* Space between the card and details */
}

/* Alternate the project layout */
.project-container.odd {
  flex-direction: row-reverse; /* For odd rows, reverse the layout */
}

/* The project card container with hover */
.project-card {
  position: relative;
  width: 500px; /* Default width */
  height: 290px;
  overflow: hidden;
  border-radius: 0px;
  background: linear-gradient(to right, rgba(0, 0, 0, 0.3), #000000);
  transition: transform 0.3s ease;
  cursor: pointer;
  animation: slideIn 2s ease-out;
  margin-top: 50px;
}

.project-card img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.project-card:hover img {
  transform: scale(1.1);
}

/* The hover details overlay inside the project card */
.project-details {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  transition: opacity 0.3s ease;
  padding: 20px;
  text-align: center;
}

.project-card:hover .project-details {
  opacity: 1;
}

/* Style for project name and duration on the opposite side */
.project-info {
  text-align: left;
  flex: 1 1; /* Takes remaining space opposite the image */
  color: white;
}

.project-info h3 {
  margin: 0;
  font-size: 1.5em;
}

.project-info p {
  font-size: 1.1em;
}

/* Modal backdrop and content */
.modal-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.project-modal {
  background: white;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.project-modal img {
  max-width: 100%;
  height: 100%;
}

h2 {
  font-size: 2.0em;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
    transform: translateY(-20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideIn {
  0% {
    opacity: 0;
    transform: translateX(-100%);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

/* Media Queries for Responsiveness */
@media (max-width: 768px) {
  .heai {
    font-size: 2em; /* Reduce font size */
    padding-top: 50px; /* Adjust padding */
  }

  .project-container {
    flex-direction: column; /* Stack elements on smaller screens */
    align-items: center; /* Center items */
    grid-gap: 20px;
    gap: 20px; /* Adjust gap for mobile */
  }

  .project-card {
    width: 90%; /* Full width on mobile */
    height: auto; /* Adjust height */
  }

  .project-info {
    text-align: center; /* Center text on mobile */
  }
}

@media (max-width: 480px) {
  .heai {
    font-size: 1.5em; /* Further reduce font size */
  }

  .project-info h3 {
    font-size: 1.2em; /* Adjust project name size */
  }

  .project-info p {
    font-size: 1em; /* Adjust project description size */
  }

  .project-card {
    width: 100%; /* Ensure full width */
    height: auto; /* Maintain aspect ratio */
  }
}

/* src/components/SkillsSection.css */

#skills {
  text-align: center;
  overflow: hidden; /* Focus on visible items */
  position: relative;
  padding: 50px 0;
  margin-left: 130px;
  margin-right: 130px;
}

.skills-list {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 70px;
  gap: 70px; /* Space between items */
  animation: scrollSkills 50s linear infinite; /* Slower scrolling animation */
}

@keyframes scrollSkills {
  0% {
    transform: translateX(100%); /* Start from left off-screen */
  }
  100% {
    transform: translateX(-100%); /* Move to right off-screen */
  }
}

.skill-card {
  margin: 10px;
  opacity: 0.5; /* Lower opacity for side items */
  transform: scale(2.8); /* Smaller size for side items */
  transition: transform 1s ease-in-out, opacity 0.5s ease-in-out; /* Smooth transitions */
}

.skill-card img {
  max-width: 100%; /* Responsive logos */
  height: auto;
}

/* Heading Styles */
.heai {
  font-family: 'Times New Roman', Times, serif;
  text-align: center;
  padding-top: 70px;
  color: #fff;
  animation: slideIn 2s ease-out;
  font-size: 3em;
  text-decoration: underline;
}

/* Keyframe for heading animation */
@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(-50px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Soft Skills Section */
.soft-skills-list {
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden; /* Focus on visible items */
  padding: 20px 0; /* Padding for top and bottom */
  animation: scrollSoftSkills 50s linear infinite; /* Slower scrolling animation */
  grid-gap: 50px;
  gap: 50px;
  font-size: 1.0em;
}

.soft-skill-item {
  margin: 10px;
  opacity: 0.5; /* Lower opacity for side items */
  transform: scale(1.2); /* Slightly smaller size for side items */
  
  /* Add box styling */
  padding: 20px; /* Space inside the box */
  border: 2px solid #fff; /* Border color */
  border-radius: 10px; /* Rounded corners */
  background: linear-gradient(to right, rgba(177, 97, 5, 0.3), #a80b93);
}

.soft-skill-item:hover {
  opacity: 1; /* Full opacity on hover */
  transform: scale(1.5); /* Larger size on hover */
}

/* Initial hidden state for skills */
.animate-on-scroll {
  opacity: 0;
  transform: translateY(20px); /* Move elements down initially */
  transition: opacity 0.6s ease-out, transform 0.6s ease-out;
}

/* Visible state */
.animate-on-scroll.visible {
  opacity: 1;
  transform: translateY(0); /* Slide up when visible */
}

/* Animation specific to soft-skill items */
.soft-skill-item.animate-on-scroll {
  opacity: 0;
  transform: scale(0.8);
  transition: opacity 0.6s ease-out, transform 0.6s ease-out;
}

.soft-skill-item.animate-on-scroll.visible {
  opacity: 1;
  transform: scale(1.2); /* Scale up when soft-skill items are visible */
}

/* Media Queries for Responsiveness */
@media (max-width: 768px) {
  #skills {
    margin-left: 20px; /* Reduce margins on mobile */
    margin-right: 20px;
  }

  .skills-list {
    flex-direction: row; /* Stack skills vertically */
    grid-gap: 20px;
    gap: 20px; /* Reduce gap for mobile */
  }

  .skill-card {
    transform: scale(1); /* Reset scale for smaller screens */
    margin: 0; /* Remove margin */
  }

  .soft-skills-list {
    flex-direction: column; /* Stack soft skills vertically */
    grid-gap: 20px;
    gap: 20px; /* Reduce gap */
  }

  .soft-skill-item {
    width: 90%; /* Full width for soft skills */
    margin: 0 auto; /* Center item */
  }

  .heai {
    font-size: 2em; /* Reduce font size for headings */
    padding-top: 50px; /* Adjust padding */
  }
}

@media (max-width: 480px) {
  .heai {
    font-size: 1.5em; /* Further reduce font size */
  }

  .skill-card {
    width: 80%; /* Ensure full width on mobile */
    height: auto; /* Adjust height */
  }

  .soft-skill-item {
    width: 95%; /* Full width on very small screens */
  }
}

